.paginate {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding-bottom: 40px;

  @media (min-width: $xs) {
    justify-content: flex-start;
    padding-bottom: 10px;
  }

  & .list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    padding-right: 23px;

    & .paginate__button {
      margin-bottom: 0;
    }

    &::before {
      content: '...';
      position: absolute;
      top: 25%;
      right: 0;
      transform: translateY(-25%);
    }
  }

  & .link {
    display: block;
    border-radius: 6px;
    background-color: #eaeced;
    padding: 8px 10px;

    @media (min-width: $xs) {
      padding: 10px 15px;
    }

    &:focus,
    &:hover {
      background-color: $blue-color;
      color: $primary-color;
    }
  }

  &__button {
    position: relative;

    margin: 0;

    &.previous,
    &.next {
      position: absolute;
      bottom: 0;
      text-transform: lowercase;
      background-color: transparent;

      @media (min-width: $xs) {
        position: relative;
      }

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background-image: url('../img/icons/arrow.svg');
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
        width: 30px;
        height: 6px;
      }

      &:focus,
      &:hover {
        background-color: transparent;
        color: $primary-color;
      }
    }

    &.previous {
      left: 15px;
      padding-left: 40px;
      padding-right: 0;
      transition: $transition-base;

      @media (min-width: $xs) {
        left: 0;
      }

      &::before {
        left: 0;
        transform: rotate(180deg) translateY(50%);
      }

      &:focus::before,
      &:hover::before {
        transition: $transition-base;
        transform: rotate(180deg) translateY(50%) translateX(10%);
      }
    }
    &.next {
      right: 15px;
      padding-right: 40px;
      padding-left: 0;

      @media (min-width: $xs) {
        right: 0;
      }

      &::before {
        right: 0;
      }

      &:focus::before,
      &:hover::before {
        transition: $transition-base;
        transform: translateY(-50%) translateX(10%);
      }
    }
  }
}
