.advantages-pro {
  height: 100%;
  position: relative;
  padding: 1.6rem;

  background-color: $yellow-color;
  border-radius: 22px;
  box-shadow: $boxShadow;
  color: $primary-color;

  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;

  @media (min-width: $xs) {
    padding: 1rem;
  }
  @media (min-width: 1024px) {
    padding: 1rem;
  }
  @media (min-width: $xl) {
    padding: 1.8rem 1rem;
  }
  @media (min-width: $xxl) {
    padding: 1.8rem;
  }

  &:focus,
  &:hover {
    color: $primary-color;
  }

  &__title {
    margin-bottom: 1rem;
    font-weight: 700;
    font-family: $mainFontBold;
    font-size: 1.6rem;
    line-height: normal;
    text-transform: uppercase;

    @media (min-width: $sm) {
      font-size: 2.6rem;
      margin-bottom: 1.6rem;
    }
    @media (min-width: $md) {
      font-size: 2rem;
      margin-bottom: 1rem;
    }

    @media (min-width: $xl) {
      font-size: 2rem;
    }

    @media (min-width: $xxl) {
      font-size: 2.5rem;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @media (min-width: $xs) {
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    @media (min-width: $md) {
      justify-content: start;
    }

    & .button {
      padding: 0.85rem 0.5rem;
      font-size: 0.75rem;
      color: $primary-color;
      border-color: $primary-color;
      text-align: center;
      flex-grow: 0.5;

      @media (min-width: $xxxs) {
        font-size: 1rem;
      }

      @media (min-width: $xs) {
        font-size: 1rem;
        padding: 1rem;
      }

      @media (min-width: $sm) {
        font-size: 1rem;
        padding: 1rem 2rem;
      }

      @media (min-width: $md) {
        font-size: 0.85rem;
        padding: 0.5rem 0.5rem;
        text-align: left;
        flex-grow: 0;
      }

      @media (min-width: $xl) {
        font-size: 1rem;
        padding: 0.75rem 1rem;
      }
    }
  }

  &__btn {
    padding: 1rem 2rem;
    margin-top: 1rem;
    text-align: center;
    background-color: $primary-color;
    border-color: $primary-color;

    &:focus,
    &:hover {
      color: $white-color;
      border-color: $red-color;
      background-color: $red-color;
    }

    @media (min-width: $sm) {
      margin-right: auto;
      padding: 0.85rem 1.6rem;
    }
  }
}
