:root {
  --advantagesItemSize: 100%;
  --advantagesListGap: 0.625rem;

  @media (min-width: $xs) {
    --advantagesListGap: 1.25rem;
    --advantagesItemSize: calc(100% / 2 - var(--advantagesListGap));
  }

  @media (min-width: $md) {
    --advantagesItemSize: calc(100% / 3 - var(--advantagesListGap));
  }

}

.subscription-advantages {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: var(--advantagesListGap);

    max-width: 1280px;
    font-size: 0.94rem;
    line-height: 130%;
    letter-spacing: 0.02em;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    flex-grow: 1;
    padding: 1.38rem;
    font-family: $mainFont;
    border-radius: 0.75rem;
    background: $blue-color;
    width : var(--advantagesItemSize);

    &-header {
      display: flex;
      flex-direction: row-reverse;
      justify-content: start;
      align-items: center;
      gap:0.75rem;
      margin-right: auto;
    }

    &-title {
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 1.06rem;
      flex-grow: 0;
      width: calc(100% - 24px - 0.25rem);
      max-width: 230px;
    }

    & .icon {
      width: 24px;
      height: 24px;
      flex-grow: 1;
    }

    &-text {
      flex-grow: 1;
      max-width: 364px;
    }

    &-tooltip {
      @include buttonReset();
    }
  }


}
