.club-services {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $blue-color;
  padding: 30px;

  margin: 1rem 0 45px;
  border-radius: $borderRadius;

  box-shadow: $boxShadow;

  &--prestige {
    background-color: $prestige-color;
  }
  &--premier {
    background-color: $premier-color;
  }

  @media (min-width: 375px) {
    padding: 30px 35px;
  }
  @media (min-width: $xs) {
    padding: 30px;
    padding-bottom: 10px;
    margin-bottom: 30px;
  }
  @media (min-width: $sm) {
    padding: 30px 52px 10px;
    margin: 1rem 0 30px;
    box-shadow: none;
  }

  @media (min-width: 800px) {
    padding: 30px 30px 10px;
  }

  @media (min-width: $md) {
    margin-bottom: 55px;
    padding: 30px 30px 10px;
  }

  @media (min-width: 1024px) {
    padding: 30px 5px 10px;
  }
  @media (min-width: 1100px) {
    // padding-left: 100px;
  }

  @media (min-width: $xl) {
    padding: 30px 30px 10px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    @include listReset();
    gap: 20px;

    justify-content: space-around;

    @media (min-width: $xs) {
      padding-bottom: 20px;
    }

    @media (min-width: 800px) {
      justify-content: center;
    }

    @media (min-width: $md) {
      width: 100%;
      gap: 30px;
    }
    @media (min-width: $xl) {
      width: 100%;
      max-width: 1200px;
      margin-left: auto;
      margin-right: auto;
      justify-content: space-around;
    }
  }

  &__item {
    flex-grow: 1;
    padding: 0;
    max-width: 100%;

    @media (min-width: $xs) {
      width: 48%;
    }

    @media (min-width: 1024px) {
      max-width: 320px;

      &.col-sm-3 {
        width: 23%;
        max-width: 23%;
      }
    }
    &--2 {
      @media (min-width: $sm) {
        order: 1;
      }
    }
    &--3 {
      @media (min-width: $sm) {
        order: 2;
      }
    }
    &--4 {
      order: 3;
      margin-bottom: 0;
    }
    &--6 {
      @media (min-width: $sm) {
        order: 1;
      }
    }
    &--7 {
      @media (min-width: $sm) {
        order: 2;
      }
    }
    &--8 {
      order: 2;
      @media (min-width: $sm) {
        order: 3;
      }

      @media (min-width: 800px) {
        order: 3;
      }
    }
  }

  &__content {
    position: relative;
    padding-left: 35px;
    &::before {
      content: '';
      z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 30px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: 0 0;
    }

    &--1 {
      &::before {
        background-image: url('../img/icons-service/gym.svg');
      }
    }
    &--2 {
      &::before {
        background-image: url('../img/icons-service/group.svg');
      }
    }
    &--3 {
      &::before {
        width: 31px;
        background-image: url('../img/icons-service/cardio.svg');
      }
    }
    &--4 {
      &::before {
        width: 36px;
        height: 36px;
        background-image: url('../img/icons-service/star.svg');
      }
    }
    &--5 {
      &::before {
        background-image: url('../img/icons-service/Swim.svg');
      }
    }
    &--6 {
      &::before {
        width: 36px;
        height: 36px;
        background-image: url('../img/icons-service/spa.svg');
      }
    }
    &--7 {
      &::before {
        width: 35px;
        height: 35px;
        background-image: url('../img/icons-service/Kids.svg');
      }
    }
    &--8 {
      &::before {
        left: 8px;
        width: 25px;
        height: 25px;

        background-image: url('../img/icons-service/ph_sun-fill.svg');
      }
    }
  }

  &__title {
    margin-bottom: 4px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
  }

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;
    max-width: 187px;

    @media (min-width: 375px) {
      max-width: 100%;
    }
    @media (min-width: $sm) {
      font-size: 20px;
    }
  }
}
