.tariffs-header {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 1rem;

  overflow: hidden;

  &__top {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    font-weight: 750;
    font-size: 1.69rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    color: #4b5052;
    max-width: 1000px;
    padding: 20px 0;

    @media (min-width: $sm) {
      padding: 20px 0;
    }

    @media (min-width: $xl) {
      max-width: 1100px;
      padding: 0 20px 40px 70px;
    }

    & h1 {
      font-family: $mainFontBold2;
      font-weight: 750;
      font-size: 1.69rem;
      line-height: 120%;
      text-transform: uppercase;

      @media (min-width: $sm) {
        font-size: 2rem;
      }
      @media (min-width: $xl) {
        font-size: 4.19rem;

      }
    }

    & .tariffs-header__price {
      font-family: $mainFontBold2;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 10px;
      font-size: 1.62rem;

       @media (min-width: $sm) {
        align-items: flex-end;
      }

      & > span {
        @media (min-width: $sm) {
          font-size: 1.69rem;
          font-weight: 750;
          font-size: 4.19rem;
          line-height: 100%;
        }
      }
    }


    
  }

  & .link {
      display: flex;
      align-items: center;
      text-align: center;
      margin-right: auto;
      border-radius: 0.75rem;
      padding: 0.62rem 1rem 0.75rem; //  
      background: linear-gradient(176deg, #e9876c 0%, #da6240 76.4%, #db7255 100%);
      color: white;
      text-decoration: none;
      border-radius: $border-radius; // 
      font-weight: 700; //  
      font-size: 1.1rem; //  
      transition: all 0.4s ease;
      letter-spacing: 0.5px; //  
      position: relative;
      overflow: hidden;

        &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
        transition: left 0.5s ease;
      }

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);

        &::before {
          left: 100%;
        }
      }

      & svg {
      width: 6.5rem;
      height: 1.5rem;
      margin-left: 0.5rem;
      }
    }


  &__info {
    position:relative;
    width: 100%;
    height: 100%;
    min-height: 400px;
    max-height: 615px;
    background-color: rgb(36, 108, 190);
  }

  &__list {
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    border-top-left-radius: 0.75rem;
    border-top-right-radius: 0.75rem;
    padding: 1.5rem;
    padding-left: 2.5rem;
    margin: 0;
    background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);

    font-size: 0.88rem;
    font-weight: 700;
    line-height: 100%;
    color: #42474c;

    @media (min-width: $sm) {
      font-size: 1.5rem;
      flex-direction: row;
      justify-content: space-between;
      padding: 1.5rem 3rem;
    }

    @media (min-width: $xl) {
      flex-direction: row;
      padding: 1.5rem 5rem;
      border-radius: 0;
    }

  }
  &__img {
    width: 100%;
    height: auto;
    object-fit: cover;
     background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);
  }
}
