:root {
  --item-number-size: 24px;
  --item-number-padding: 0;
  --item-left-padding: 12px;

  @media (min-width: $sm) {
    --item-number-size: 32px;
    --item-number-padding: 2px;
    --item-left-padding: 1rem;
  }

}

.subscription-steps {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 1rem;

  @media (min-width: $sm) {
    flex-direction: row;
    justify-content: space-between;

  }

  &__list {
    list-style-type: none;
    margin: -22px 0 0 0;
    padding: 0;

    @media (min-width: $sm) {
      width : calc(50% - 1rem);
      margin: -22px 0;
    }

    & li {
      position: relative;
      counter-increment: my-counter;
      padding: 22px 0 22px calc(var(--item-number-size) + var(--item-left-padding));
      font-family: $mainFontBold;
      font-size: 1.06rem;
      line-height: 130%;
      letter-spacing: 0.02em;

      &:not(:last-child) {
        border-bottom: 1px solid $gray-color;
      }

      &::before {
        content: counter(my-counter);
        position: absolute;
        left:0;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        background: #42474d;
        color: white;
        width: var(--item-number-size);
        height: var(--item-number-size);
        text-align: center;
        padding: var(--item-number-padding);
      }

      @media (min-width: $sm) {
        font-size: 1.25rem;
      }
    }
  }

  &__picture {
    width: 100%;
    height: 100%;
    min-height: 230px;
    border-radius: 1.38rem;
    background-color: $gray-color;
    background-image: url('../img/podpiska/steps.webp');
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;

    @media (min-width: $sm) {
      width : calc(50% - 1rem);
      height: auto;
    }
  }

}

