.page-subscription {
  font-family: $mainFont;

  &__title {
    margin-bottom: 24px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 130%;
    text-transform: uppercase;
    color: #42474d;

    @media (min-width: $sm) {
      margin-bottom: 35px;
      font-size: 2.5rem;
    }

    & span {
      display: block;
      @media (min-width: $xs) {
        display: inline-block;
      }
    }
  }

  &--indent {
    margin-bottom: 60px;

    @media (min-width: $sm) {
      margin-bottom: 80px;
    }
    @media (min-width: $md) {
      margin-bottom: 100px;
    }
  }

  &__header {
    padding-top: 20px;
    font-weight: 700;
    font-size: 1.25rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #9fa1a3;

    @media (min-width: $sm) {
      font-size: 1.56rem;
    }
    @media (min-width: $md) {
      font-size: 1.69rem;
    }

    & h1 {
      font-family: 'Avenir Next Cyr Demi', sans-serif;
      font-weight: 700;
      font-size: 2rem;
      line-height: 130%;
      letter-spacing: 0.02em;
      text-transform: uppercase;

      color: #42474d;

      @media (min-width: $sm) {
        font-size: 3.12rem;
      }
    }

    &-price {
      position: relative;
      padding-left: 25px;
      display: inline-flex;

      @media (min-width: $sm) {
        padding-left: 33px;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 30px;
        height: 100%;
        background-size: 100% 100%;
        background-image: url('../img/podpiska/icons/icon-rub-bold.svg');
        background-repeat: no-repeat;
        background-position: 0 0;

        @media (min-width: $sm) {
          width: 45px;
        }
      }
    }

    &-picture {
      margin-top: 1.6rem;
      width: 100%;
      height: 200px;
      border-radius: 1.38rem;
      background-color: $gray-color;
      background-image: url('../img/podpiska/main.webp');
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;

      @media (min-width: $sm) {
        margin-top: 2rem;
        height: 265px;
      }
      @media (min-width: $md) {
        margin-top: 2.2rem;
        height: 400px;
        background-position: center top;
      }
    }
  }

}

/*   */
.tab-content {
  .tab-pane {
    transition: opacity 0.3s ease, transform 0.3s ease;
    opacity: 0;
    transform: translateY(5px);

    &.active.show {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

/*     */
#nestedTab {
  .nav-link {




    &.active {
      background-color: rgba(0,0,0,0.05);
    }
  }
}

/*  Bootstrap- */
.accordion {
  --bs-accordion-transition: all 0.4s cubic-bezier(0.65, 0, 0.35, 1);

  .accordion-body {
    padding: 0 0.25rem;
    font-family: $mainFont;
    font-weight: 400;
    color: #42474d;
  }

  .accordion-item {
    background-color: transparent;
    font-weight: 700;
    font-size: 0.94rem;
    line-height: 120%;
    text-transform: uppercase;
    color: #42474d;
    border: none;
    border-bottom: 1px solid;
    border-radius: 0;

    @media (min-width: $md) {
      font-size: 1.25rem;
    }
  }

  .accordion-collapse {
    transition: var(--bs-accordion-transition) !important;
    text-transform: none;
    font-family: $mainFont;
  }

  .accordion-button {
    transition: var(--bs-accordion-transition);
    background-color: transparent;
    font-family: $mainFontBold;
    text-transform: uppercase;
    padding: 1rem 0;
    color: #42474d;
    font-size: 1.06rem;

    @media (min-width: $sm) {
      font-size: 1.25rem;
    }


    &::after {
      transition: var(--bs-accordion-transition) !important;
      background-image: url('../img/podpiska/icons/icon-plus.svg');
      background-size: 32px 32px;
      background-position: center center;
      background-repeat: no-repeat;
      width: 40px;
    }

    &:focus {
      box-shadow: none;
    }
  }

  .accordion-button:not(.collapsed) {
    color: #42474d;
    background-color: transparent;

    &:after {
      transform: rotate(-45deg);
    }
  }

  /*    */
  .accordion.accordion-flush {
    border: 1px solid var(--bs-accordion-border-color);
    border-radius: var(--bs-accordion-border-radius);

    .accordion-body {
      padding: 1rem 0.5rem;
      max-width: 960px;
      font-family: $mainFont;
      font-weight: 400;
    }

    .accordion-item {
      border-left: 0;
      border-right: 0;
      &:last-child {
        border-bottom: 0;
      }
    }

    .accordion-button {
      border-radius: 0;
      padding: 0.5rem 0.25rem;
      font-size: 1.06rem;

      line-height: 120%;
      text-transform: none;

      @media (min-width: $sm) {
        font-size: 1.25rem;
      }
    }
  }
}


.tariff-tooltip, .trade-in-tooltip, .advantages-tooltip {
  &.tooltip.show {
    opacity: 1;
  }

  .tooltip-inner {
    background: white;
    padding: 0.5rem;
    box-shadow: 4px 4px 12px 0 rgba(66, 71, 77, 0.1);
    border-radius: 0.75rem;
    font-family: $mainFont;
    font-weight: 400;
    font-size: 0.81rem;
    line-height: 130%;
    color: #42474d;
    opacity: 1;
  }

  .tooltip-arrow::before {
    border-color: transparent;
    border-top-color: white;
  }
}

.subscription-tabs {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  border: none;
  transition: all 0.3s ease-in-out;

  & .nav-link {
    border-radius: 1rem;
    padding: 1.06rem 1.56rem;
    box-shadow: 8px 8px 16px 0 rgba(73, 76, 79, 0.19);
    background: #e2f0fb;
    color: #42474d;


    &:hover {
      background: $yellow-color;
    }

    &.active {
      background: $yellow-color;
    }
  }


}

.subscription-tab-content {
  &.tab-content {
    background: #e2f0fb;
    border-radius: 1.38rem;
    overflow: hidden;
  }

  &__pane {
    display: flex;
    flex-direction: column-reverse;

    @media (min-width: $xs) {
      flex-direction: row-reverse;
      min-height: 560px;
    }
  }

  &__header {
    font-size: 1.06rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    color: #42474d;

    & h3 {
      font-size: 1.06rem;
      font-weight: 700;
      font-family: $mainFontBold;

      @media (min-width: $sm) {
        font-size: 1.25rem;
      }
    }
  }

  &__picture {
    height: auto;
    min-height: 200px;
    border-radius: 1.38rem;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-color: #384032;

    @media (min-width: $xxxs) {
      min-height: 290px;
    }

    @media (min-width: $xxs) {
      min-height: 370px;
    }

    @media (min-width: $xs) {
      width: 55%;
      height: auto;
    }

    &--trade-in {
      background-color: #e6d491;
      background-image: url('../img/podpiska/sale.webp');
    }

    &--student {
      background-image: url('../img/podpiska/sale-student.webp');
    }

    &--dr {

      background-image: url('../img/podpiska/sale-dr.webp');
    }
  }

  &__info {
    padding: 1.5rem 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @media (min-width: $xs) {
      width: 45%;
      padding: 1.4rem;
    }

    @media (min-width: $md) {
      padding: 2.5rem;
    }
  }

  &__header {

  }

  &__tooltip {
    @include buttonReset();
    display: inline-flex;
  }

  & .form {
    display: flex;
    flex-direction: column;
  }

  & .form-control {
    padding: 0.75rem 0.75rem 0.75rem 1.35rem;
  }

  & .form-label {
    padding-bottom: 1rem;

    @media (min-width: $xs) {
      padding-bottom: 0.75rem;

    }
  }

  & .form-check {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0;
    margin-bottom: 1rem;
  }

  & .form-check-label {
    width: calc(100% - 25px);
    font-size: 0.9rem;
    line-height: 130%;
  }
}

.form-message-modal__title {
  text-align: center !important;
}
