.page-tariffs__app {
  max-width: 80rem;
  margin: 0 auto;
  overflow: hidden;

  @media (min-width: $sm) {
    padding: 2rem 0 0;
  }

  @media (min-width: $xl) {
    padding: 7rem 0 0;
  }

  & .app-link {
  position: relative;
  display: block;
  padding: 1.3rem;
  margin-top: 40px;
  height: 8.31rem;
  box-shadow: inset 0 -7px 44px 0 rgba(255, 255, 255, 0.26);
  background: linear-gradient(174deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);
  text-decoration: none;
  font-family: $mainFontBold;
  font-weight: 700;
  font-size: 1rem;
  line-height: 115%;
  text-transform: uppercase;
  color: #fff;

  @media (min-width: $sm) {
    font-size: 2.06rem;
    height: 17.31rem;
    border-radius: 1.5rem;
  }

  @media (min-width: $md) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 3rem 2rem;
    font-size: 3rem;
  }

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: -20px;
    width: 60%;
    height: 130%;
    background: url('../../img/podpiska-2/about/app.png');
    background-repeat: no-repeat;
    background-position: bottom right;
    background-size: contain;

    @media (min-width: $sm) {

    }

    @media (min-width: $xl) {
      bottom: 0;
      right: 0;
      width: 50%;
      height: 140%;
    }
  }

  & span {
    display: block;
    max-width: 50%;
  }
}

}

.text-oferta {
  font-weight: 400;
  font-size: 0.62rem;
  line-height: 130%;
  color: rgba(0, 0, 0, 0.6);
  padding: 1.5rem 1rem;
  margin: 0;

  @media (min-width: $sm) {
    font-size: 0.75rem;
    max-width: 750px;
  }

  @media (min-width: $xl) {
    padding: 1.5rem 1rem;
  }
}


