.page-tariffs__club-gallery {
  position: relative;
  width: 100%;
  border-radius: 0;

  @media (min-width: $breakpoint-md) {
    flex: 0 0 45%;
  }

  & .container {
    display: flex;
    flex-direction: column;
    max-width: 1440px;
    margin: 0 auto;
    padding: 0;
  }

  & .swiper-container {
    overflow: hidden;
  }

  & .swiper-slide {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: var(--bg-color);
    color: var(--text-color);

    border-radius: 0;

    @media (min-width: $sm) {
      &::before {
        content: '';
        z-index: 0;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to top, var(--bg-color) 10%, transparent 100%);
      }

      &:hover {
        &::before {
          background: linear-gradient(to top, var(--bg-color) 10%, transparent 100%);
        }
      }
    }

    @media (min-width: $xl) {
      min-height: 800px;

      &::before {
        background: linear-gradient(to top, var(--bg-color) 30%, transparent 100%);
      }

      &:hover {
        &::before {
          background: linear-gradient(to top, var(--bg-color) 30%, transparent 100%);
        }
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.5s ease;
    }
  }

  & .slide-gallery__img {
    width: 100%;
    height: 100%;
    object-fit: cover;

    @media (min-width: $xl) {
      min-height: 800px;
    }
  }

  & .slide-gallery__info {
    position: absolute;
    bottom: 20px;
    left: 0;
    width: 100%;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 120%;
    color: var(--text-color);
    text-align:center;

    @media (min-width: $breakpoint-md) {
      text-align: left;
      font-size: 2.5rem;
    }
  }

  & .slide-gallery__title {
    text-align:center;
    padding: 0 20px;
    margin-bottom: 15px;
    font-family: $mainFontBold2;
    font-weight: 700;
    font-size: 19px;
    text-transform: uppercase;
    color: var(--text-color);
    max-width: 1000px;

    @media (min-width: $sm) {
      font-size: 28px;
      padding: 0 40px;
      margin-bottom: 30px;
    }
    @media (min-width: $xl) {
      font-size: 40px;
      padding: 0 40px 0 120px;
      text-align:left;
    }
  }

    & .slide-gallery__text {
      text-align:center;
      padding: 20px;
      background: rgba(255, 255, 255, 0.14); //   
      font-weight: 700;
      font-size: 14px;
      line-height: 120%;
      color: var(--text-color);

      @media (min-width: $sm) {
        font-size: 20px;
        padding: 16px 40px;
      }
      @media (min-width: $xl) {
        font-size: 40px;
        padding: 30px 40px;
        max-width: 100%;
        text-align:left;
      }

      & span {
        display: block;
        max-width: 500px;
        margin: 0 auto;

        @media (min-width: $xl) {
          max-width: 1200px;
        }
      }
    }

  & .swiper-button-next,
  & .swiper-button-prev {
    top: 70%;
    color: var(--text-color);
    background: rgba(255, 255, 255, 0.7);
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-top: -30px;
    backdrop-filter: blur(5px);

    @media (min-width: $sm) {
      width: 40px;
      height: 40px;
    }
    @media (min-width: $md) {
      top: 50%;
      width: 50px;
      height: 50px;
    }

    &::after {
      font-size: 14px;
      font-weight: bold;
      color: #4b5052;

      @media (min-width: $sm) {
        font-size: 24px;
      }
    }

    &:hover {
      background: rgba(255, 255, 255, 0.9);
      transform: scale(1.1);
    }

    &.swiper-button-disabled {
      opacity: 0.3;
      pointer-events: none;
    }
  }

  & .swiper-pagination {
    bottom: 0;
    width: 100%;

    &-bullet {
      background: var(--text-color);
      border-color: var(--text-color);
      opacity: 0.7;
      width: 20px;
      height: 4px;
      margin: 0 3px;
      border-radius: 23px;
      transition: all 0.3s ease;

       @media (min-width: $breakpoint-md) {
        margin: 0 4px;
      }

      &-active {
        opacity: 1;
        transform: scale(1.3); //   
        background: var(--text-color); //   
      }
    }
  }
}

