.page-sale {
  &.container {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__slider {
    width: 100%;

    margin-bottom: 40px;
    @media (min-width: $sm) {
      min-height: 300px;
      margin-bottom: var(--page-sale-mb);
    }
  }
  &__form {
    width: 100%;

    margin-bottom: var(--page-sale-mb);
  }
  &__list {
    width: 100%;

    margin-bottom: var(--page-sale-mb);

    @media (min-width: $md) {
      width: 50%;
    }
  }
  &__box-kids {
    width: var(--page-sale-w-size);
    min-height: 100%;
    margin-bottom: var(--page-sale-mb);

    & .section {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;

      @media (min-width: $md) {
        min-height: 360px;
      }
    }
  }
  &__box-korus {
    min-height: 100%;
    width: var(--page-sale-w-size);
    margin-bottom: var(--page-sale-mb);

    & .section {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;

      @media (min-width: $md) {
        min-height: 360px;
      }
    }
  }
  &__content {
    width: 100%;
  }

  &__timer {
    width: 100%;
    margin-bottom: var(--page-sale-mb);
  }
}
