
.feedback-form {
  @media (min-width: $md) {
    padding: 0 50px;
  }

  &__wrap {
    
    backdrop-filter: blur(32px);
    background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);
    padding: 1rem 0;
    overflow: hidden;

    @media (min-width: $xs) {
      border-radius: 1.5rem;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 50%;
        height: 100%;
        background-image: url("../../img/podpiska-2/bg-form.webp");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        border-top-right-radius: 1.5rem;
        border-bottom-right-radius: 1.5rem;
        overflow: hidden;
      }
    }

    @media (min-width: $xs) {
      padding: 1.5rem;
      padding-right: 0.5rem;
    }

    @media (min-width: $md) {
      padding: 35px 1rem;
      &::before {
        display: none;
      }
    }

    
  }

  & .container {
    max-width: 768px;
    margin: 0 auto;

    @media (min-width: $xs) {
      width: 50%;
      transform: translateX(50%);
    }

    @media (min-width: $md) {
      width: 100%;
      transform: translateX(0);
    }
  }

  & .list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    font-weight: 500;
    font-size: 1rem;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: #fff;
    margin-bottom: 1rem;
  }

  & .button[type='submit'] {
    position: relative;
      display: block;
      cursor: pointer;
      text-align: center;
      margin-right: auto;
      border-radius: 0.75rem;
      padding: 0.62rem 1rem 0.75rem; //  
      
      color: white;
      font-weight: 700;
      font-size: 1.06rem;
      transition: all 0.4s ease;
      box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.25);
      background: linear-gradient(176deg, #e9876c 0%, #da6240 76.4%, #db7255 100%);
      overflow: hidden;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: -100%;
          width: 100%;
          height: 100%;
          background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
          transition: left 0.5s ease;
        }

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);

        &::before {
          left: 100%;
        }
      }
    
  }

  & .form__btns {
    gap: 10px;

    @media(min-width: $xl) {
      flex-direction: row-reverse;
      gap: 10px;
      align-items: center;
    }

    & .button {
      // flex-grow: 1;
      width: 100%;
    }
  }
}
