.tariffs-carousel {
  display: flex;
  gap: 0.5rem;
  width: 100%;
  justify-content: space-between;

  @media (min-width: $sm) {
    gap: 1rem;
  }
}

.tariff {
  position: relative;
  background: #e2f0fb;
  border-radius: 1.38rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  flex-grow: 1;
  min-width: 280px;

  @media (min-width: 375px) {
    min-width: 290px;
  }

  @media (min-width: $sm) {
    padding: 22px;
    min-width: 360px;
  }

  &--yellow {
    background: #fffeda;
  }

  &__tooltip {
    @include buttonReset();
    position: absolute;
    top: 1rem;
    right: 1rem;
  }

  &__title {
    display: flex;
    flex-direction: column;
    margin: 0;
    font-weight: 700;

    font-family: $mainFontBold;
    font-size: 1.25rem;
    line-height: 120%;
    text-transform: uppercase;

    color: #42474D;

    @media (min-width: $sm) {
      font-size: 1.69rem;

    }

    & span {
      font-family: $mainFont;
      font-weight: 400;
      font-size: 1.06rem;
      text-transform: lowercase;

      @media (min-width: $sm) {
        font-size: 1.25rem;
      }
    }
  }

  &__services {
    display: flex;
    flex-direction: column;
    gap: 10px;

    list-style-type: none;
    padding: 0;
    margin: 0;

  }

  &__service {
    position: relative;
    padding-left: 21px;
    font-size: 0.94rem;
    line-height: 130%;
    color: #42474D;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-image: url('../img/podpiska/icons/icon-check.svg');
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: contain;
      width: 16px;
      height: 16px;
    }

    &--cancel {
      color: #9fa1a3;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        background-image: url('../img/podpiska/icons/icon-cancel.svg');
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
        width: 16px;
        height: 16px;
      }
    }

    &--bold {
      font-family: $mainFontBold;
      font-weight: 700;
    }

    &-tooltip {
      @include buttonReset();

      & .icon {
        width: 16px;
        height: 16px;
      }
    }

  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    text-decoration: none;
    border-radius: 1rem;
    margin: auto 0 0 0;
    padding: 0.62rem 1.56rem 0.75rem 1.56rem;
    box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.25);
    min-height: 56px;
    background: #42474d;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 0.94rem;
    line-height: 130%;
    color: #fff;

    transition: opacity 0.3s ease-in-out, color 0.3s ease-in-out;
    cursor: pointer;

    @media (min-width: $sm) {
      font-size: 1.06rem;
    }

    @media (min-width: $md) {
      justify-content: center;
      font-size: 1.25rem;
    }

    &:hover {
      opacity: 0.7;
      color: white;


      & span::before {
        right: calc((-1) * (1rem + 33px));
      }
    }

    &--red {
      background: #cd4141;
    }

    & span {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 51%;
        right: calc((-1) * (0.5rem + 33px));
        transform: translateY(-50%);
        background-image: url('../img/podpiska/icons/icon-arrow.svg');
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
        width: 33px;
        height: 8px;

        transition: right 0.3s ease-in-out;
      }
    }
  }
}
