.form-book {
  padding: 2.5rem 1rem;

  @media (min-width: $xs) {
    padding: 35px;
  }

  @media (min-width: $md) {
    padding: 25px 30px 25px 20px;
    // padding-top: 25px;
  }
  @media (min-width: $xxl) {
    padding: 25px;
    padding-right: 30px;
  }

  & .title {
    padding-bottom: 1rem;

    font-size: 18px;
    line-height: 130%;

    @media (min-width: $xxs) {
      font-size: 22px;
    }
    @media (min-width: $xs) {
      padding-bottom: 36px;
    }
    @media (min-width: $sm) {
      font-size: 24px;
    }
    @media (min-width: $md) {
      font-size: 18px;
      line-height: 100%;
      padding-bottom: 1rem;
    }
    @media (min-width: $xl) {
      font-size: 22px;
      line-height: 100%;
    }

    @media (min-width: $xxl) {
      padding-bottom: 1.5rem;
      line-height: 130%;
      font-size: 24px;
    }
  }

  &__wrapper {
    display: grid;

    column-gap: 20px;
    row-gap: 10px;

    @media (min-width: $xs) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width: $md) {
      gap: 0;
      grid-template-columns: repeat(1, 1fr);
    }
  }

  & .form-label {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    flex-grow: 1;
    padding-bottom: 0.5rem;

    @media (min-width: $md) {
      padding-bottom: 8px;
    }
    @media (min-width: $xxl) {
      padding-bottom: 1rem;
    }

    &[for='formName'] {
      @media (min-width: $xs) {
        order: 1;
      }
      @media (min-width: $md) {
        order: 0;
      }
    }
  }

  & .form__label-name {
    @media (min-width: $md) {
      padding-bottom: 4px;
    }
  }

  & .form-control {
    align-items: flex-start;
  }

  & .form-select,
  & .form-control {
    margin-bottom: 0;
    padding: 1rem 2rem 1rem 1.45rem;

    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: rgba(66, 71, 77, 0.5);

    @media (min-width: $md) {
      padding: 0.5rem 2rem 0.5rem 1rem;
      font-size: 16px;
    }
    @media (min-width: $xl) {
      padding: 0.5rem 2rem 0.5rem 1.45rem;
      font-size: 18px;
    }
    @media (min-width: $xxl) {
      padding: 1rem 2rem 1rem 1.45rem;
      font-size: 20px;
    }
  }

  & .form-control::placeholder {
    font-size: 20px;
    padding: 1.175rem 0 1.175rem 0;

    @media (min-width: $md) {
      padding: 0.5rem 0 0.5rem 0;
      font-size: 16px;
    }
    @media (min-width: $xl) {
      padding: 0.75rem 0;
      font-size: 18px;
    }
    @media (min-width: $xxl) {
      padding: 1rem 0 1rem 0;
      font-size: 20px;
    }
  }

  & .form-check-input:checked {
    background-color: #888888;
    border-color: #888888;
    margin: 0;
  }

  & button[type='submit'] {
    margin-top: 0;
    padding: 16px 29px;
    max-width: 100%;
    text-align: center;
    font-size: 20px;
    width: 100%;

    @media (min-width: $xs) {
      padding: 18px 29px;
    }

    @media (min-width: $md) {
      padding: 6px 29px;
    }

    @media (min-width: $xl) {
      padding-top: 0.8rem;
      padding-bottom: 0.8rem;
      padding: 13px 25px;
      width: auto;
    }

    @media (min-width: $xxl) {
      margin-right: auto;
      margin-left: 0;
    }
  }

  &__btns {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: auto;
    margin-bottom: auto;
    padding-right: 0;
    padding-top: 1rem;
    width: var(--field-width);
    width: 100%;

    @media (min-width: $xs) {
      order: 1;
      padding-top: 35px;
    }
    @media (min-width: $md) {
      order: 0;
      width: 100%;
      padding-right: 0;
      padding-top: 0.5rem;
    }
    @media (min-width: $xl) {
      width: 100%;
      padding-right: 0;
      padding-top: 0.5rem;
    }

    @media (min-width: $xxl) {
      padding-top: 1rem;
    }

  }

  &__mess {
    margin-top: 0.5rem;
    line-height: 100%;
    font-size: 16px;
    color: #888888;

    @media (min-width: $xs) {
      font-size: 14px;
    }
    @media (min-width: $md) {
      font-size: 14px;
    }

    & a {
      color: #888888;
    }
  }
}
