.full-schedule {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 30px;

  overflow: hidden;
  scroll-behavior: smooth;

  @media (min-width: $md) {
    margin-top: -30px;
  }

  &__icon-scroll {
    position: absolute;
    top: 5px;
    right: 0;
    width: 32px;
    height: 30px;

    @media (min-width: $md) {
      display: none;
    }
  }

  &__inner {

    @media (max-width: 990px) {
      min-width: 990px;
      width: 100%;
      overflow-x: auto;
    }

    @media (max-width: $xs) {
      min-width: min-content;
      max-width: 700px;
    }
  }

  // &__header {
  //   display: flex;
  //   gap: 10px;
  // }

  &__footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    gap: 10px;
    padding-top: 20px;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;

    @media (min-width: $xs) {
      justify-content: space-between;
      flex-direction: column;
      align-items: flex-end;
    }
    @media (min-width: $md) {
      flex-direction: row;
      align-items: center;
    }
  }

  &__item-count {
    display: inline-block;
    width: auto;
    margin-right: auto;
    margin-bottom: 20px;
    padding: 10px 20px;
    background: #eaeced;
    border-radius: 12px;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;

    & select {
      background-color: transparent;
      border: none;
      color: $primary-color;
    }
  }

  &__paginate {
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: $primary-color;

    & .link {
      color: $primary-color;
    }
  }

