.hero {
  font-family: $mainFontBold;
  font-weight: 700;
  line-height: 130%;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 25px;
    margin-bottom: 35px;

    @media (min-width: $xs) {
      margin-top: 14px;
      margin-bottom: 35px;
    }
    @media (min-width: $md) {
      margin-top: 38px;
      margin-bottom: 54px;
    }
  }
  &__clubs-info {
    display: flex;
    margin-bottom: 25px;
    width: 100%;

    @media (min-width: $xs) {
      margin-bottom: 35px;
      padding-right: 75px;
      padding-left: 4px;
    }

    @media (min-width: $md) {
      margin-bottom: 0;
      padding-right: 0;
      width: 40%;
    }

    @media (min-width: $xxl) {
      width: 40%;
    }
  }

  &__link,
  &__pic {
    width: 100%;

    @media (min-width: $md) {
      width: 60%;
    }
  }

  &__pic {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    @media (min-width: $md) {
      padding: 0 0 0 3rem;
    }

    & picture {
      box-shadow: $boxShadow;
      border-radius: $borderRadius;
      overflow: hidden;
      height: 100%;
    }

    & img {
      max-width: 100%;
      height: 100%;
      box-shadow: $boxShadow;
      border-radius: $borderRadius;

      @media (min-width: $md) {
        box-shadow: none;
      }

      @media (max-width: $md) {
        width: 100%;
        max-height: 270px;
      }
    }

    & picture {
      width: 100%;
    }
  }

  &__link {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    @media (min-width: $md) {
      padding: 0 0 0 3rem;
    }

    & picture,
    & img {
      box-shadow: $boxShadow;
      border-radius: $borderRadius;
      overflow: hidden;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 130%;
    font-size: 20px;

    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        375px: 20px,
        768px: 50px,
        1440px: 60px,
      )
    );

    @media (min-width: $xs) {
      // padding: 0 16px;
      padding-left: 4px;
    }

    @media (min-width: $sm) {
      color: rgba(73, 76, 79, 0.5);
    }
    @media (min-width: $md) {
      margin-bottom: 1rem;
    }

    & span {
      display: block;

      @media (min-width: $md) {
        display: inline-block;
      }
    }
  }
}
