.popover {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 320px;
  background: #f6f7f8;
  box-shadow: 0px 4px 4px 1px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  height: auto;
  max-height: 300px;

  @media (min-width: $sm) {
    min-width: 380px;
    max-height: 233px;
  }

  & .popover-body {
    height: auto;
    max-height: 300px;
    @media (min-width: $sm) {
      min-width: 380px;
      max-height: 233px;
    }
  }

  & .lesson__popover-close {
    // @include linkReset();
    position: absolute;
    top: 10px;
    right: 12px;
    width: 20px;
    height: 20px;
    background-image: url('../img/icons/close.svg');
    background-size: contain;
    cursor: pointer;
  }

  & .title {
    padding-right: 15px;
    margin-bottom: 5px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 13px;
    line-height: 130%;
  }

  & .text {
    padding-right: 15px;
    margin-bottom: 5px;
    font-family: $mainFont;
    font-weight: 400;
    font-size: 13px;
    line-height: 130%;
    height: 100%;
    max-height: 170px;

    overflow-y: auto;
    // overflow: hidden;

    @media (min-width: $sm) {
      max-height: 130px;
    }

    @media (min-width: $md) {
      max-height: 110px;
    }

    @media (min-width: $xl) {
      max-height: 100px;
    }

    &::-webkit-scrollbar {
      width: 7px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.7);
      border-radius: 8px;
    }

    &::-webkit-scrollbar-thumb {
      background: #c4c4c4;
      border-radius: 41px;
    }
  }

  & .lesson__link {
    display: inline-flex;
    margin-top: auto;
    margin-bottom: 0;
    height: 30px;
  }

  & .avatar {
    display: flex;
    gap: 10px;
    align-items: center;
    margin-bottom: 10px;
  }

  & .avatar__pic {
    width: 55px;
    height: 54px;
    border-radius: 50%;
    overflow: hidden;
    object-fit: cover;
    background-color: $blue-color;
  }
}
