// 
$breakpoint-md: 768px;
$breakpoint-lg: 1024px;
$transition-duration: 0.5s;
$border-radius: 1.38rem; //   12px   

.page-tariffs {
  & .container-fluid {
    max-width: 1440px;
    margin: 0 auto;
  }

  & .title {
    text-transform: uppercase;
    font-weight: 750;
    font-size: 1.25rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    text-align: center;
    color: #4b5052;
    padding: 2rem 1rem;

    @media (min-width: $sm) {
      font-size: 1.69rem;
      padding: 50px 1rem;
    }
  }

  & .modal-submit-form {

    .btn-close {
      & svg {
        display: none;
      }

      &::before {
        font-size: 250%;
        color: rgba(0, 0, 0, .25);
        background-color: transparent;
        left: -30%;
      }
    }

    & .modal-content {
      border-radius: 0.75rem;
      max-width: 400px;
      margin: 0 auto;
    }

    & .modal-body {

      font-weight: 700;
      font-size: 0.75rem;
      line-height: 100%;
      text-align: center;

      border-radius: 0.75rem;
      box-shadow: 0 4px 11px 0 rgba(0, 0, 0, 0.25);
      background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);
    
        & svg {
          z-index: 0;
          width: 36px;
          height: 36px;
          fill: #e1ebf0;
        }
    
    }

    & .form-message-modal {
    padding-bottom: 0;
    }

    & .form-message-modal__title {
      padding: 1rem; 
      margin-top: -1rem;
      font-weight: 700;
      font-size: 0.88rem;
      line-height: 100%;
      text-align: center;
      border-radius: 0.38rem;
      border: 2px solid #3080a3;
    }
    & .form-message-modal__text {
      padding: 1rem;
    }

    & .social {
      display: flex;
      flex-direction: row;
      gap: 1rem;

      &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.5rem;

        & .icon {
          width: 30px;
          height: 30px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        

        &--vk {
          & .icon {
            background-image: url('../../img/podpiska-2/icons/vk.svg');
          }
        }
        &--dz {
          & .icon {
            background-image: url('../../img/podpiska-2/icons/dzen.svg');
          }
        }
        &--tm {
          & .icon {
            background-image: url('../../img/podpiska-2/icons/telegram.svg');
          }
        }

        & a {
          display: block;
        }
      }
    }
  }
}
