.page-clubs {
  position: relative;
  &--region {
    & .hero {
      margin-bottom: 35px;

      @media (min-width: $md) {
        margin-bottom: 54px;
      }
    }

    & .subscription {
      margin-bottom: 40px;

      @media (min-width: $xs) {
        margin-bottom: 54px;
      }
      @media (min-width: $xl) {
        margin-bottom: 100px;
      }
    }
  }
}
