
.page-tariffs__map-clubs {
  & iframe {
    width: 100%;
    height: 400px;
    background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);

    @media(min-width: $sm) {
      height: 500px;
    }
  }

}
.page-tariffs__map-club {
  & iframe {
    width: 100%;
    height: 300px;
    background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);
  }
}

.map-title {
  font-family: $mainFontBold;
  font-weight: 700;
  font-size: 0.88rem;
  line-height: 140%;
  text-align: center;

  padding: 30px;

  @media(min-width: $sm) {
    font-size: 1.62rem;
    padding: 40px 80px;
    line-height: 180%;
  }

  @media(min-width: $xl) {
    font-size: 3.25rem;
    padding: 100px 16px;
    line-height: 140%;
  }

  &__clubs {
    font-weight: 750;
    font-size: 1.5rem;

    @media(min-width: $sm) {
      font-size: 3.19rem;
    }

    @media(min-width: $xl) {
      font-size: 5.25rem;
    }
  }

  &__city {
    font-family: $mainFont;
    font-size: 0.88rem;

    @media(min-width: $sm) {
      font-size: 1.62rem;
    }

    @media(min-width: $xl) {
      font-size: 2.5rem;
    }
  }

  &__city-dop {
    font-size: 0.88rem;

    @media(min-width: $sm) {
      font-size: 3.19rem;
    }

    @media(min-width: $xl) {
      font-size: 3.25rem;
    }
  }

  &__link {
    text-decoration: underline;
  }
}

.map-sidebar {
  display: flex;
  flex-direction: column;
  gap: 5px;

  padding: 1rem;
  backdrop-filter: blur(20px);
  background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);
  color: #42474c;

  @media(min-width: $sm) {
    position: absolute;
    top: 0;
    right: 0;
    width: 300px;
    height: 100%;
  }

  @media(min-width: $md) {
    width: 400px;
  }

  &__club {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1rem;
    line-height: 110%;

    @media(min-width: $sm) {
      font-weight: 900;
      font-size: 1.38rem;
    }

    @media(min-width: $xl) {
      font-size: 1.62rem;
    }
  }
  &__address {
    margin: 0;
    font-weight: 500;
    font-size: 0.88rem;
    line-height: 129%;
  }

  &__link {
    font-weight: 500;
    font-size: 0.88rem;
    line-height: 129%;
    color: inherit;
    text-decoration: none;
  }

  &__work {
    margin-top: auto;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 0.88rem;
    line-height: 140%;
  }

  &__table {
    font-weight: 400;
    font-size: 0.75rem;
    line-height: 140%;
    text-transform: uppercase;
    border-collapse: collapse;
    margin-right: auto;

    & td {
      padding: 0.25rem 0.5rem;
    }
  }
}
