.schedule-table {
  width: 100%;
  min-width: 630px;
  text-align: left;
  position: relative;
  border-spacing: 0 11px;
  border-collapse: inherit;

  &__title {
    position: relative;
    padding: 10px;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;

    @media (min-width: $sm) {
      padding: 10px 20px;
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 20px;
      line-height: 130%;
    }

    &--sort span {
      position: relative;
      padding-right: 20px;
      cursor: pointer;
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 15px;
        height: 26px;
        background-image: url('../img/icons/sort.svg');
        background-repeat: no-repeat;
        background-position: 0 0;
      }
    }
  }

  &__row:nth-child(2n) td {
    background-color: #eaeced;
  }

  // & th {
  //   background: transparent;
  //   position: sticky;
  //   top: 0; /* Don't forget this, required for the stickiness */
  //   // box-shadow: 0 2px 2px -1px rgba(0, 0, 0, 0.4);
  // }

  &__column {
    // position: relative;
    padding: 10px;
    margin: 10px;

    font-weight: 400;
    font-size: 15px;
    line-height: 140%;

    background: #dee8ed;

    @media (min-width: $sm) {
      padding: 20px;
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
    }

    &:first-child {
      border-radius: 12px 0 0 12px;
    }

    &:last-child {
      border-radius: 0 12px 12px 0;
    }

    &--place {
      width: 130px;
      min-width: 130px;
    }

    &[data-bs-toggle="popover"] {
      text-decoration: underline;

      @media (min-width: $md) {
        text-decoration: none;
      }
    }
  }

  &__date {
    display: flex;
    flex-wrap: wrap;
    max-width: 50px;

    @media (min-width: $sm) {
      display: inline-flex;
      max-width: 100%;
    }
  }

  &__time {
    @media (min-width: $sm) {
      display: flex;
    }
  }

  &__column[data-type='workout'] {
    & .schedule-table__inner {
      @media (min-width: $xs) {
        width: 208px;
        min-width: 208px;
      }
    }
  }
  &__column[data-type='trainer'] {
    & .schedule-table__inner {
      @media (min-width: $xs) {
        width: 150px;
        min-width: 150px;
      }
    }
  }

  &__inner {
    display: flex;
    gap: 10px;

    &:focus,
    &:hover {
      cursor: pointer;
    }

    & .icon {
      display: none;
      width: 18px;
      height: 17px;

      @media (min-width: $md) {
        display: flex;
      }
    }

    & .text {
      width: 100%;

      @media (min-width: $md) {
        width: calc(100% - 18px - 10px);
      }
    }
  }
}
